import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import simple_cad_image from "./images/simple_cad.jpeg";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post10() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["cad"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <>
      <Helmet>
        <title>2D CAD Desktop Application</title>
        <meta name="description" content="2D CAD Desktop Application" />
        <meta name="keywords" content="cad" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="2D CAD Desktop Application" />
        <meta property="og:description" content="2D CAD Desktop Application" />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                2D CAD Desktop Application
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Sep 6, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I developed a simple 2D CAD desktop application. Computer
                  Aided Design (CAD) applications are used for creating
                  engineering and architectural designs and drawings (to name a
                  few).
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The application focuses on drawing two-dimensional shapes in a
                  canvas. The user must create a new Project for starting a new
                  drawing. In addition, the application provides the Save and
                  Open Project features, to store and load the project data.
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img src={simple_cad_image} alt="Simple CAD"></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The application allows the user to create shapes in the canvas
                  using mouse actions. The drawing procedure is initiated on the
                  first mouse click in the canvas (after selecting the icon that
                  corresponds to the shape), and the graphical object is
                  re-shaped on mouse movement. Currently, just the Line and
                  Curve shapes are available.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Each graphical object on the canvas is represented by its
                  shape and the points that define the shape. The user can
                  optionally hide the points associated with the shapes in the
                  canvas. The drawing procedure provides additional features:
                  select the closest point (when the mouse pointer is close to
                  another shape), and draw a straight line for the Line shape.
                  The canvas does not provide a grid.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  After a shape has been added to the canvas, the user can
                  select the shape using the mouse click, and access to its
                  properties. From the properties panel the user can update the
                  coordinates of the shape. As an alternative approach, the user
                  can modify the shape re-initiating the drawing procedure with
                  the mouse interaction.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The application does not allow to create layers or groups of
                  objects, however I introduced the concept of Tags for the
                  shapes. The user can define a list of Tags on Project level,
                  and then assign specific Tags to each shape. From the Tags
                  filter panel, the user can select one Tag, as a result all of
                  the shapes that are not associated with the specified Tag are
                  greyed out in the canvas.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following is the GitHub repository of the CAD application:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/simple-cad"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/simple-cad
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Overall, the application is extremely simple, and does not
                  provide all the features that you can expect from commercial
                  CAD applications. In the previous article I talked about some
                  of the technical aspects of the application. This project has
                  been a good exercise to experiment with JavaFX, and to work on
                  the design of the architecture for a CAD application.
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post10;
