import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import api_connection_image from "./images/api_connection.jpeg";
import api_connection_request_image from "./images/api_connection_request.jpeg";
import logic_app_connection_image from "./images/logic_app_connection.jpeg";
import "./css/Post18.css";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-yaml";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post18() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "logic-app"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Logic App with ARM API Connection</title>
        <meta name="description" content="Logic App with ARM API Connection" />
        <meta name="keywords" content="azure, logic-app" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="Logic App with ARM API Connection" />
        <meta
          property="og:description"
          content="Logic App with ARM API Connection"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Logic App with ARM API Connection
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Dec 7, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Azure Logic App is a service used for creating workflows in
                  Azure, and provides connectors for many Azure services and
                  external services.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  One of these Azure services is the Azure Resource Manager,
                  that provides different actions to be used in the Logic App
                  workflow, for example "List resources by subscription". The
                  Azure Resource Manager actions create one API Connection
                  resource with connector name "arm".
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  During the creation of the API Connection from the Azure
                  Portal, you need to provide one Authentication method, you can
                  choose between OAuth, Service principal, and Managed identity.
                  In this post I will focus on the Managed Identity
                  Authentication. You can choose either a System Assigned
                  Managed Identity or a User Assigned Managed Identity in the
                  Managed Identity dropdown.
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img
                    src={logic_app_connection_image}
                    alt="Logic App Connection"
                  ></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The API Connection resource in the Azure Portal specifies that
                  the Authentication is of type Managed Identity. In addition,
                  the configuration of the API Connection shows a banner that
                  reminds that the API Connection can only be used with a
                  Managed Identity.
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img src={api_connection_image} alt="API Connection"></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The creation of the API Connection from the Azure Portal is
                  quite simple. Let's now investigate how to create the API
                  Connection resource using Infrastructure as Code.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The API Connection resources serve different purposes, for
                  example the Logic App can create API Connections for Gmail,
                  Azure Service Bus, or Azure Resource Manager. The ARM Template
                  schema of the Azure resource "Microsoft.Web/connections" [1]
                  provides different parameters to customize the API Connection
                  resource. Using the inspect network feature in the browser, we
                  can see that the operation for creating a new API Connection
                  configured with Managed Identity from the Azure Portal sends
                  the following request:
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img
                    src={api_connection_request_image}
                    alt="API Connection request"
                    className="api_connection_request_image"
                  ></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  From the request payload we can see that the field
                  "parameterValueType" is configured with value "Alternative".
                  It's interesting to notice that the field "parameterValueType"
                  does not appear on the list of parameters in the ARM Template
                  definition of the API Connection resource. If you try to use
                  the exported ARM Template from the API Connection resource
                  created in the Azure Portal, you will not be able to create a
                  new API Connection resource with Authentication Managed
                  Identity, unless you specify the parameter
                  "parameterValueType" with value "Alternative", like in the
                  following code snippet:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-yaml">
                    "resources": &#91;&#123;
                    <br />
                    &nbsp;&nbsp;"type": "Microsoft.Web/connections",
                    <br />
                    &nbsp;&nbsp;"apiVersion": "2016-06-01",
                    <br />
                    &nbsp;&nbsp;...
                    <br />
                    &nbsp;&nbsp;"properties": &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;"api": &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"name": "arm",
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&#125;,
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;"parameterValueType": "Alternative",
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;"parameterValueSet": &#123;&#125;
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    &#125;&#93;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The Terraform library AzureRM allows you to create Azure API
                  Connection resources using the Terraform resource
                  "azurerm_api_connection" [2], however it does not provide any
                  parameters to assign the value for "parameterValueType". As a
                  workaround, you can use the Terraform resource
                  "azurerm_template_deployment" [3] for provisioning the API
                  Connection resource using the ARM Template definition.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I created one example project with the use case described in
                  this blog post. The GitHub repository is the following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/logic-app-workflows/tree/main/logic-app-arm-test"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/logic-app-workflows
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://learn.microsoft.com/en-us/azure/templates/microsoft.web/connections?pivots=deployment-language-arm-template"
                    target="_blank"
                    rel="noreferrer"
                  >
                    microsoft.web/connections
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://registry.terraform.io/providers/hashicorp/azurerm/4.13.0/docs/resources/api_connection"
                    target="_blank"
                    rel="noreferrer"
                  >
                    azurerm_api_connection
                  </a>
                  <br />
                  [3]{" "}
                  <a
                    className="underline"
                    href="https://registry.terraform.io/providers/hashicorp/azurerm/4.13.0/docs/resources/resource_group_template_deployment"
                    target="_blank"
                    rel="noreferrer"
                  >
                    azurerm_template_deployment
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post18;
