import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-javascript";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post7() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["story-outline", "vue"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Web Application Frontend in Vue.js</title>
        <meta name="description" content="Web Application Frontend in Vue.js" />
        <meta name="keywords" content="story-outline, vue" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta
          property="og:title"
          content="Web Application Frontend in Vue.js"
        />
        <meta
          property="og:description"
          content="Web Application Frontend in Vue.js"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Web Application Frontend in Vue.js
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Sep 1, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I decided to explore the Vue framework because I was
                  interested to understand what could have been the effort to
                  create a project from scratch without prior knowledge on the
                  platform.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In this post I will introduce the Vue framework and I will
                  discuss some features that can speed up approaching Vue, in
                  particular for people that have experience working with React.
                  It's not scope of this article to compare Vue with other
                  frameworks.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Vue was initially designed for rapid prototyping of web
                  applications, but is well suited for developing large-scale
                  rective web applications. The command{" "}
                  <code>npm create vue@latest</code> scaffolds a new Vue
                  application. The core features of Vue are declarative
                  rendering and reactivity. The following are the definitions
                  from the Vue documentation [1]:
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  <b>Declarative Rendering:</b> Vue extends standard HTML with a
                  template syntax that allows us to declaratively describe HTML
                  output based on JavaScript state.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  <b>Reactivity:</b> Vue automatically tracks JavaScript state
                  changes and efficiently updates the DOM when changes happen.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Vue uses an HTML-based template syntax that allows you to
                  declaratively bind the rendered DOM to the underlying
                  component instance's data. The single-file component (SFC)
                  format encapsulates the component's logic (JavaScript),
                  template (HTML), and styles (CSS) in a single{" "}
                  <code>.vue</code> file.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Both the React and Vue frameworks share some concepts: Virtual
                  DOM, component-based UI, routing, and state management. Some
                  of the most interesting features of Vue are the Composition
                  API, the lifecycle hooks, and Typescript support.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The Composition API structures the component logic in
                  functions, and is typically used with{" "}
                  <code>&lt;script setup&gt;</code> for the single-file
                  component format. The functions handle the reactive state
                  variables and can be combined to create complex scenarios.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The lifecycle hooks are functions that can be used to add your
                  own code at specific stages of the initialization of the
                  component.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Vue provides first-class Typescript support. Typescript brings
                  type safety on top of the Javascript code. The code is more
                  reliable and less prone to errors via static analysis at build
                  time.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following is an example that combines all of the concepts
                  described above:
                </p>
                <pre className="mt-4 text-left h-96 w-full">
                  <code className="language-javascript">
                    &lt;script setup lang="ts"&gt;
                    <br />
                    &nbsp;&nbsp;import &#123; ref, onMounted &#125; from 'vue'
                    <br />
                    &nbsp;&nbsp;import axios from 'axios'
                    <br />
                    <br />
                    &nbsp;&nbsp;interface Project &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;'projects/id': string
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;'projects/name': string
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;'projects/description': string
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;'projects/tags': string&#91;&#93;
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    <br />
                    &nbsp;&nbsp;const projects =
                    ref&lt;Project&#91;&#93;&gt;(&#91;&#93;)
                    <br />
                    <br />
                    &nbsp;&nbsp;const getProjects = async () =&gt; &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;await axios
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.get&lt;Project&#91;&#93;&gt;('/api/projects')
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.then(async (response)
                    =&gt; &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;if
                    (response.status !== 200) &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;return
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;const data =
                    await response.data
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;projects.value
                    = data
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;)
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.catch((error) =&gt;
                    &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;console.error(error)
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;)
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    <br />
                    &nbsp;&nbsp;onMounted(async () =&gt; &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;await getProjects()
                    <br />
                    &nbsp;&nbsp;&#125;)
                    <br />
                    &lt;/script&gt;
                    <br />
                    <br />
                    &lt;template&gt;
                    <br />
                    &nbsp;&nbsp;&lt;v-list&gt;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&lt;v-list-item v-for="(project,
                    index) in projects"&gt;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&lt;v-list-item-title&gt;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#123;&#123;
                    project&#91;'projects/name'&#93; &#125;&#125;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&lt;/
                    v-list-item-title&gt;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&lt;/ v-list-item&gt;
                    <br />
                    &nbsp;&nbsp;&lt;/ v-list&gt;
                    <br />
                    &lt;/ template&gt;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The Composition API, the lifecycle hooks, and Typescript
                  support, provide a really smooth transition from React to Vue.
                  I found extremely easy to approach Vue, with a prior
                  experience working on React web applications.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I used Vue as the frontend web app of the the story outlining
                  web application:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/book-advisor"
                    target="_blank"
                    rel="noreferrer"
                  >
                    github.com/sitMCella/book-advisor
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The web application makes also use of the Axios HTTP client
                  library and the Vuetify [2] component framework.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://vuejs.org/guide/introduction.html"
                    target="_blank"
                    rel="noreferrer"
                  >
                    vuejs.org
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://vuetifyjs.com/en/introduction/why-vuetify/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    vuetifyjs.com
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post7;
