import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import book_advisor_map_image from "./images/book_advisor_map.jpeg";
import book_advisor_scene_image from "./images/book_advisor_scene.jpeg";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post8() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["story-outline"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Story Outlining Web Application</title>
        <meta name="description" content="Story Outlining Web Application" />
        <meta name="keywords" content="story-outline" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="Story Outlining Web Application" />
        <meta
          property="og:description"
          content="Story Outlining Web Application"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Story Outlining Web Application
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Sep 2, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I developed a story outlining web application. A story outline
                  defines the roadmap for a story, and provides a structure for
                  writing a manuscript, guiding the writer from the beginning to
                  the end.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Some of the components of a story outline are the characters
                  definition, the plots, and the scenes. A plot consists of the
                  sequence of events that constitute the story. A scene instead
                  is composed of a series of continuous (self-contained) events,
                  and is part of a plot. The scenes of a story can also be
                  grouped in different chapters of the manuscript.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The web application represents a book as a Project. The
                  project consists of a title, a description, and a list of
                  Tags. The Tags are configured at Project level, in order to
                  maintan consistency over the Tags assigned to the scenes of
                  the story. You can decide for example to create one Tag for
                  every character, and later on track on which scenes each
                  character appears throughout the story.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I decided to provide two different structures for the story
                  outline in the Project Map page and the Project Book page.
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img
                    src={book_advisor_map_image}
                    alt="Book Advisor Map"
                  ></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The Project Map page defines a storyboard that collects the
                  scenes in a tabular format with the chapters as columns and
                  the plots as rows. The scenes are cards that shows the extract
                  of the scene, and can be expanded to get access to the content
                  and the configuration of the scene. The Project Map page
                  allows you to plan and structure the scenes in plots and
                  chapters. In addition you can assign Tags to the scenes and
                  filter the scenes by Tags.
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img
                    src={book_advisor_scene_image}
                    alt="Book Advisor Scene"
                  ></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The Project Book page instead shows the list of scenes. In
                  this page the scene editor is always visible, but you can't
                  configure the scenes or filter the scenes by Tag.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The GitHub repository of the story outlining web application
                  is the following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/book-advisor"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/book-advisor
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In this article I described the scope and the concepts of the
                  story outlining web application. I have already covered in
                  previous posts the technical details of the web application.
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post8;
