import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-clojure";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post6() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["story-outline", "clojure"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Web Applications with Clojure</title>
        <meta name="description" content="Web Applications with Clojure" />
        <meta name="keywords" content="story-outline, clojure" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="Web Applications with Clojure" />
        <meta
          property="og:description"
          content="Web Applications with Clojure"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Web Applications with Clojure
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Aug 30, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In one of the last projects, I used Clojure as the backend for
                  a web application. I decided to explore Clojure because of the
                  concise and functional approach of the language.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  You can choose between different frameworks and libraries for
                  creating web applications in Clojure. I decided to use the
                  Ring [1], Compojure [2], and Component [3] libraries. For
                  bootstrapping the application, I found extremely helpful the
                  User Manager example project created by Sean Corfield:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/seancorfield/usermanager-example/blob/develop/src/usermanager/main.clj"
                    target="_blank"
                    rel="noreferrer"
                  >
                    seancorfield/usermanager-example
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In this article I will provide an overview of these libraries,
                  and a description of how the libraries work together in a web
                  application.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Ring provides an abstraction over the HTTP protocol. The HTTP
                  requests and responses are managed via Clojure hashmaps. Some
                  of the basic concepts of the Ring library are handlers,
                  request, response, and middleware. The handlers are functions
                  applied to a request and that produce a response.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In the following example, the <i>get-projects</i> handler
                  calls the <i>model/get-projects</i> function, that retrieves
                  data from the database. The <i>get-projects</i> handler
                  returns the data in json format:
                </p>
                <pre className="mt-4 text-left h-58 w-full">
                  <code className="language-clojure">
                    (defn get-projects
                    <br />
                    &nbsp;&nbsp;"Retrieve the projects."
                    <br />
                    &nbsp;&nbsp;[request]
                    <br />
                    &nbsp;&nbsp;(let [projects (model/get-projects
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;(-&gt; request
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:application/component
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:database))]
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;(json-response projects)))
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Middleware are functions that are applied before or after the
                  handler is executed. The User Manager example project makes
                  use of Ring middleware to add functionality to the handlers.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The purpose of the following middleware is to inject the
                  application component in the request before calling the
                  handler:
                </p>
                <pre className="mt-4 text-left h-48 w-full">
                  <code className="language-clojure">
                    (defn- add-app-component
                    <br />
                    &nbsp;&nbsp;[handler application]
                    <br />
                    &nbsp;&nbsp;(fn [req]
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;(handler
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(assoc req
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:application/component
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;application))))
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Component is a Clojure framework for managing the lifeycle and
                  the dependencies of software components which have runtime
                  state. Every component implements the lifecycle protocol
                  (start and stop methods), and each one can depend on other
                  components.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The application component used inside the Ring middleware in
                  the previous example, can be configured with a dependency to
                  the database component:
                </p>
                <pre className="mt-4 text-left h-92 w-full">
                  <code className="language-clojure">
                    (defrecord Application
                    <br />
                    &nbsp;&nbsp;[config database state]
                    <br />
                    &nbsp;&nbsp;component/Lifecycle
                    <br />
                    &nbsp;&nbsp;(start [this]
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;(assoc this :state "Running"))
                    <br />
                    &nbsp;&nbsp;(stop [this]
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;(assoc this :state "Stopped")))
                    <br />
                    <br />
                    (defn application-component
                    <br />
                    &nbsp;&nbsp;[config]
                    <br />
                    &nbsp;&nbsp;(component/using
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;(map-&gt;Application
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&#123;:config config&#125;)
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;[:database]))
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The handlers will then be able to use the database connection
                  from the application component, injected by the middleware in
                  the request.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Compojure is a routing library for Ring. The routes defined in
                  Compojure return Ring handler functions. Routes can be
                  combined and evaluated in order.
                  <br />
                  The following is an example of routes:
                </p>
                <pre className="mt-4 text-left h-42 w-full">
                  <code className="language-clojure">
                    (defroutes app
                    <br />
                    &nbsp;&nbsp;(GET "/api/projects" []
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;(wrap #'get-projects))
                    <br />
                    &nbsp;&nbsp;(route/resources "/")
                    <br />
                    &nbsp;&nbsp;(route/not-found "Not Found")
                    <br />)
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In this article I described how the Ring, Compojure, and
                  Component libraries can be combined to create a structured web
                  application in Clojure. I used these frameworks for creating
                  the backend of the story outlining web application:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/book-advisor"
                    target="_blank"
                    rel="noreferrer"
                  >
                    github.com/sitMCella/book-advisor
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The web application makes also use of the next.jdbc library
                  [4] as a Clojure wrapper to access the database.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://github.com/ring-clojure/ring"
                    target="_blank"
                    rel="noreferrer"
                  >
                    ring-clojure/ring
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://github.com/weavejester/compojure"
                    target="_blank"
                    rel="noreferrer"
                  >
                    weavejester/compojure
                  </a>
                  <br />
                  [3]{" "}
                  <a
                    className="underline"
                    href="https://github.com/stuartsierra/component"
                    target="_blank"
                    rel="noreferrer"
                  >
                    stuartsierra/component
                  </a>
                  <br />
                  [4]{" "}
                  <a
                    className="underline"
                    href="https://github.com/seancorfield/next-jdbc"
                    target="_blank"
                    rel="noreferrer"
                  >
                    seancorfield/next-jdbc
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post6;
