import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-javascript";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post11() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["turing-machine", "angular", "rxjs"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Reactive Frontend with RxJS</title>
        <meta name="description" content="Reactive Frontend with RxJS" />
        <meta name="keywords" content="turing-machine, angular, rxjs" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="Reactive Frontend with RxJS" />
        <meta property="og:description" content="Reactive Frontend with RxJS" />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Reactive Frontend with RxJS
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Sep 7, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  RxJS is a library that brings the reactive programming
                  paradigm to Javascript. I used the RxJS library in a web
                  application developed with Angular.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  RxJS is an implementation of Observables in Javascript [1].
                  Reactive and asynchronous programming are really useful for
                  building modern web applications. RxJS simplifies managing
                  streams of data, combining operations, repeating failed
                  operations, and cancel asynchronous operations.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The essential concepts in RxJS are: Observable, Observer,
                  Subscription, Operators, and Subject. An Observable represents
                  a stream of data, or to be more precise an invokable
                  collection of future values. An Observer can subscribe to one
                  Observable in order to receive new values. Operators are
                  functions that generate a new Observable and are used to
                  transform, filter, and combine data. A Subject is a kind of
                  Observable that allows multiple subscribers to receive the
                  same values.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In the following example I created a service that generates
                  values that are displayed asynchronously in a web page.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The class MachineStatus is a data structure that represents
                  the status of a machine:
                </p>
                <pre className="mt-4 text-left h-48 w-full">
                  <code className="language-javascript">
                    export class MachineStatus &#123;
                    <br />
                    &nbsp;&nbsp;public configName: string;
                    <br />
                    <br />
                    &nbsp;&nbsp;constructor(configName: string) &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.configName = configName;
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    &#125;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The class Configuration represents one specific configuration
                  of a machine:
                </p>
                <pre className="mt-4 text-left h-92 w-full">
                  <code className="language-javascript">
                    export class Configuration &#123;
                    <br />
                    &nbsp;&nbsp;public name: string;
                    <br />
                    &nbsp;&nbsp;public finalConfigName: string;
                    <br />
                    <br />
                    &nbsp;&nbsp;constructor(name: string, finalConfigName:
                    string) &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.name = name;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.finalConfigName =
                    finalConfigName;
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    <br />
                    &nbsp;&nbsp;public evolve(): MachineStatus &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;return new
                    MachineStatus(this.finalConfigName);
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    &#125;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following code defines the service AlgorithmService. The
                  service produces an Observable of type BehaviorSubject. The
                  values of type MachineStatus are added to the Observable using
                  the time interval service.
                </p>
                <pre className="mt-4 text-left h-96 w-full">
                  <code className="language-javascript">
                    import &#123; Injectable &#125; from "&#64;angular/core";
                    <br />
                    import &#123;
                    <br />
                    &nbsp;&nbsp;Observable,
                    <br />
                    &nbsp;&nbsp;BehaviorSubject,
                    <br />
                    &nbsp;&nbsp;interval
                    <br />
                    &#125; from "rxjs";
                    <br />
                    <br />
                    @Injectable()
                    <br />
                    export class AlgorithmService &#123;
                    <br />
                    &nbsp;&nbsp;private machineStatus:
                    BehaviorSubject&lt;MachineStatus&gt;;
                    <br />
                    &nbsp;&nbsp;private configurations: Configuration[];
                    <br />
                    &nbsp;&nbsp;private configuration: Configuration;
                    <br />
                    &nbsp;&nbsp;private actualStatus: MachineStatus;
                    <br />
                    <br />
                    &nbsp;&nbsp;constructor() &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;const initial =
                    newMachineStatus("s");
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.machineStatus =
                    newBehaviorSubject(initial);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.configurations = [];
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.configuration =
                    newConfiguration("","");
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.actualStatus =
                    newMachineStatus("");
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    <br />
                    &nbsp;&nbsp;public evolve(
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;configurations:
                    Array&lt;Configuration&gt;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;): Observable&lt;MachineStatus&gt;
                    &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.configurations =
                    configurations;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.configuration =
                    configurations[0];
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.actualStatus = new
                    MachineStatus(this.configuration.name);
                    <br />
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.evolution();
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;return
                    this.machineStatus.asObservable();
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    <br />
                    &nbsp;&nbsp;private evolution(): void &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;const source = interval(1000);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;source.subscribe(() =&gt; &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;this.actualStatus =
                    this.configuration.evolve();
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;this.machineStatus.next(this.actualStatus);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;this.configuration =
                    this.configurations.filter(
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;c =&gt;
                    c.name = this.actualStatus.configName)[0];
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&#125;);
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    &#125;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following code represents the component TapeComponent. The
                  component initializes a BehaviorSubject object and executes
                  the algorithm service, that will add the generated values
                  asynchronously to the object.
                </p>
                <pre className="mt-4 text-left h-96 w-full">
                  <code className="language-javascript">
                    import &#123; Component &#125; from "@angular/core";
                    <br />
                    import &#123;
                    <br />
                    &nbsp;&nbsp;Observable,
                    <br />
                    &nbsp;&nbsp;BehaviorSubject,
                    <br />
                    &nbsp;&nbsp;take
                    <br />
                    &#125; from "rxjs";
                    <br />
                    <br />
                    @Component(&#123;
                    <br />
                    &nbsp;&nbsp;selector: "app-tape",
                    <br />
                    &nbsp;&nbsp;templateUrl: "./tape.component.html"
                    <br />
                    &#125;)
                    <br />
                    export class TapeComponent &#123;
                    <br />
                    &nbsp;&nbsp;private machineStatuses: MachineStatus[]
                    <br />
                    &nbsp;&nbsp;private subject:
                    BehaviorSubject&lt;MachineStatus[]&gt;;
                    <br />
                    &nbsp;&nbsp;public machineStatus:
                    Observable&lt;MachineStatus[]&gt;;
                    <br />
                    <br />
                    &nbsp;&nbsp;constructor(private algorithm: AlgorithmService)
                    &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.machineStatuses = []
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.subject = new
                    BehaviorSubject(this.machineStatuses);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.machineStatus =
                    this.subject.asObservable();
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.evolveAlgorithm();
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    <br />
                    &nbsp;&nbsp;private evolveAlgorithm(): void &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;const firstConfiguration = new
                    Configuration("b","o");
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;const secondConfiguration = new
                    Configuration("o","o");
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;const configurations =
                    [firstConfiguration, secondConfiguration];
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.algorithm
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.evolve(configurations)
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.pipe(take(5))
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.subscribe(
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(machineStatus)
                    =&gt; &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;this.machineStatuses.push(machineStatus);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;this.subject.next(this.machineStatuses);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;);
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    &#125;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following HTML snippet is associated to the TapeComponent
                  and shows how the values inserted in the Observable object are
                  retrieved and displayed in the web page.
                </p>
                <pre className="mt-4 text-left h-25 w-full">
                  <code className="language-javascript">
                    &lt;div *ngFor="let status of machineStatus | async"&gt;
                    <br />
                    &nbsp;&nbsp;&lt;span&gt;&#123;&#123; status.configName
                    &#125;&#125;&lt;/span&gt;
                    <br />
                    &lt;/div&gt;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The previous example is a simplified version of the Turing
                  machine catalog web application:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/turing-machine"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/turing-machine
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The web application makes use of the RxJS library and Angular
                  to display the machine configuration and the tape associated
                  with the evolution of a Turing machine.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The running web application is accessible from the following
                  link:
                  <br />
                  <a
                    className="underline"
                    href="https://sitmcella.github.io/turing-machine/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/turing-machine/
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://rxjs.dev/guide/overview"
                    target="_blank"
                    rel="noreferrer"
                  >
                    rxjs.dev
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post11;
