import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post15() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "function", "ssl"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <>
      <Helmet>
        <title>SSL certificate renewal in Azure</title>
        <meta name="description" content="SSL certificate renewal in Azure" />
        <meta name="keywords" content="azure, function, ssl" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="SSL certificate renewal in Azure" />
        <meta
          property="og:description"
          content="SSL certificate renewal in Azure"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                SSL certificate renewal in Azure
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Oct 11, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Azure Application Gateways support SSL termination by allowing
                  SSL certificates to be added to the listeners in the
                  Application Gateway configuration.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In this post I will describe a solution for the renewal of SSL
                  certificates issued by Let's Encrypt. The solution makes use
                  of an automation procedure using PowerShell and Azure
                  Function. Terraform is used for provisioning the solution in
                  Azure.
                  <br />
                  The GitHub project is the following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/azure-terraform-ssl-certificate-renewal"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/azure-terraform-ssl-certificate-renewal
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The automation procedure generates new SSL Certificates using
                  Let's Encrypt and stores the SSL certificates in a certificate
                  object in Azure Key Vault. The SSL certificate is then used by
                  one Application Gateway listener associated to a web
                  application hosted in one Azure App Service.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The SSL Certificates must include a domain name. You can
                  purchase a custom domain from a domain name registrar such as
                  Namecheap. The SSL certificates issued by Let's Encrypt are
                  valid for 90 days, and Let's Encrypt recommends to renew the
                  SSL certificates every 60 days. The procedure for generating a
                  new SSL certificate requires the use of an ACME client. The
                  client module ACME-PS is available in PowerShell.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The automation procedure executes the following steps: creates
                  a new ACME order, stores an ACME challenge in a Storage
                  Account, generates a new SSL certificate, and imports the PFX
                  file of the SSL certificate in one Azure Key Vault. The
                  PowerShell script with the automation procedure can be found
                  in:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/azure-terraform-ssl-certificate-renewal/blob/main/modules/ssl_certificate_renewal/function/TimerTrigger1/run.ps1"
                    target="_blank"
                    rel="noreferrer"
                  >
                    function/TimerTrigger1/run.ps1
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In order to issue one SSL certificate, a new ACME order must
                  be created and a challenge must be generated and stored as a
                  file to ensure the ownership of the domain name that is going
                  to be included in the SSL certificate.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In addition, the ACME challenge file must be reachable from
                  the Internet using the custom domain name. I decided to store
                  the ACME challenge in one Azure Storage Account configured as
                  a Static Website. One Azure DNS Zone is provisioned with the
                  name of the custom domain, and a CNAME record is configured
                  with a subdomain of the custom domain. In the example project
                  the subdomain is called "app". In order to host your custom
                  domain in Azure DNS, you have to configure the name servers
                  (NS) of the Azure DNS Zone in the domain name registrar as NS
                  records.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Initially the CNAME record in the Azure DNS Zone maps the
                  subdomain of the custom domain to the FQDN name of the static
                  website. This temporary DNS configuration is used while
                  generating the first SSL certificate. The ACME challenge must
                  be stored as a blob in the Storage Account inside a folder
                  with name ".well-known/acme-challenge" in the container
                  "$web".
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  When the PowerShell script in the Azure Function generates the
                  first SSL certificate and stores the certificate in the Key
                  Vault, then the Azure Application Gateway can be configured as
                  follows: one listener is configured with the SSL Certificate
                  stored in the Key Vault, and is associated to the web
                  application backend; a second listener is configured with the
                  HTTP protocol, and is associated to the Static Website
                  backend. The CNAME record in the Azure DNS Zone is then
                  configured with the Public IP Address of the Azure Application
                  Gateway.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  This ensures that Let's Encrypt will be able to access the
                  challenge stored in the Static Website for every subsequent
                  executions of the PowerShell script hosted in the Azure
                  Function.
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post15;
