import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import document_versions_image from "./images/document_versions.jpeg";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post17() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["document-versions"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Document Versions web application</title>
        <meta name="description" content="Document Versions web application" />
        <meta name="keywords" content="document-versions" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="Document Versions web application" />
        <meta
          property="og:description"
          content="Document Versions web application"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Document Versions web application
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Oct 18, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Document Versions is a web application that manages documents
                  using Git and provides a text editor for editing the
                  documents.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I created the web application using Rect on the frontend, and
                  Rust on the backend. The GitHub repository of the web
                  application is the following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/doc-versions"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/doc-versions
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The application is bundled with Docker images and is executed
                  using Docker Compose. The documents managed by the web
                  application are hosted in the local machine. The root path can
                  be set in the file "configuration.yaml" as part of the backend
                  application configuration. The current version of the
                  application does not support hosting the documents in external
                  storages.
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img
                    src={document_versions_image}
                    alt="Document Versions"
                  ></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The user of the application works with documents collected in
                  a Workspace. The creation of a new Workspace initializes a
                  directory managed via Git. Inside a Workspace the user can
                  create, edit, and delete files, and the versions of the
                  documents are stored in separate Git commits. Git maintains
                  the full history of the files, and the Save operation on the
                  documents creates a new commit in the current branch. The user
                  can in alternative decide to create a new Git branch in the
                  Save operation. This second approach is useful for creating a
                  document draft or maintaining different variants of the
                  documents.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The backend application aims to provide an interface to the
                  Git client, and the web application binds the Git commands to
                  actions associated with Workspaces, branches and documents.
                  The backend application uses the Rust git2-rs library [1],
                  that contains bindings to the libgit2 C library, which is used
                  to manage git repositories.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The web application consists of a text editor, a left panel
                  with the documents organization, and a right panel with a Git
                  graph. The text editor provided by the web application uses
                  the library React Draft Wysiwyg [2] for editing RTF documents,
                  and the library Draft.js [3] for editing simple text
                  documents.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The left panel consists of three sections: Workspaces,
                  Branches, and Directory. The Workspaces section allows the
                  user to manage the Workspaces. The Branches section lists the
                  Git branches in the selected Workspace. The Directory section
                  lists the files contained in the selected branch. The text
                  editor shows the content of the selected document from the
                  Directory section.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The web application also provides a panel with a Git graph
                  history, that displays branches and commits. I used the
                  Javascript library GitGraph.js [4] for drawing the Git graph.
                  I created a procedure to order the branches based on the Git
                  commit dependencies. The Git graph is interactive: selecting a
                  commit from a specific branch, the action triggers the left
                  panel to switch to the selected branch. The current version of
                  the web application will always use the latest commit of the
                  selected branch.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://github.com/rust-lang/git2-rs"
                    target="_blank"
                    rel="noreferrer"
                  >
                    rust-lang/git2-rs
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://jpuri.github.io/react-draft-wysiwyg/#/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    react-draft-wysiwyg
                  </a>
                  <br />
                  [3]{" "}
                  <a
                    className="underline"
                    href="https://draftjs.org/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    draftjs
                  </a>
                  <br />
                  [4]{" "}
                  <a
                    className="underline"
                    href="https://www.nicoespeon.com/gitgraph.js/#0"
                    target="_blank"
                    rel="noreferrer"
                  >
                    gitgraph.js
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post17;
