import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-javascript";
import "prismjs/components/prism-yaml";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post21() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "kubernetes", "opentelemetry", "grafana"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Telemetry Traces with Grafana Tempo</title>
        <meta
          name="description"
          content="Telemetry Traces with Grafana Tempo"
        />
        <meta
          name="keywords"
          content="azure, kubernetes, opentelemetry, grafana"
        />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta
          property="og:title"
          content="Telemetry Traces with Grafana Tempo"
        />
        <meta
          property="og:description"
          content="Telemetry Traces with Grafana Tempo"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Telemetry Traces with Grafana Tempo
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Mar 1, 2025
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Infrastructure observability is becoming essential with the
                  increasing complexity of the applications, developed using
                  microservices or serverless architectures, and provisioned on
                  distributed systems managed by cloud providers and
                  orchestration platforms such as Kubernetes.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  OpenTelemetry is an observability framework designed to manage
                  telemetry data such as traces, metrics, and logs [1].
                  OpenTelemetry is not an observability backend like Prometheus,
                  instead focuses on the generation, collection, management, and
                  export of telemetry data. OpenTelemetry provides libraries for
                  different programming languages for instrumenting the
                  application to emit signals. The applications send the
                  telemetry data to a backend that processes and stores the
                  signals.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  OpenTelemetry Collector [2] is a proxy that can receive,
                  process, and export telemetry data. It supports receiving
                  telemetry data in multiple formats (for example OTLP, Jaeger,
                  and Prometheus), and sending data to one or more backends. It
                  also supports processing and filtering telemetry data before
                  it gets exported.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Grafana Tempo [3] is a distributed tracing backend, that can
                  ingest common open source tracing protocols, including Jaeger,
                  Zipkin, and OpenTelemetry. Grafana Tempo requires just an
                  object storage to operate, used for the long term storage of
                  the traces. Grafana Tempo does not index the traces, in order
                  to provide a scalable and cost-effective solution.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Grafana [4] is a platform used to query and visualise metrics,
                  logs, and traces. Grafana natively supports Grafana Tempo
                  providing the built-in Tempo data source.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I created a project that showcases the usage of these
                  frameworks hosted in one Azure Kubernetes Service resource.
                  The high level design of the solution is the following: a
                  Python application, instrumented with the OpenTelemetry
                  library, sends the telemetry data to OpenTelemetry Collector
                  configured with the OTLP receiver. OpenTelemetry Collector
                  dispatches the traces to Grafana Tempo using the OTLP
                  receiver. Grafana Tempo stores the traces in one Azure Storage
                  Account. To complete the configuration, a Tempo data source
                  can be created in Grafana to connect to Grafana Tempo and
                  visualise the traces.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The GitHub repository of the project is the following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/opentelemetry-traces-grafana-tempo"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/opentelemetry-traces-grafana-tempo
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  OpenTelemetry Collector, Grafana Tempo, and Grafana are
                  configured and deployed in Kubernetes using the following Helm
                  Charts:
                  <br />
                  <a
                    className="underline"
                    href="https://artifacthub.io/packages/helm/opentelemetry-helm/opentelemetry-collector"
                    target="_blank"
                    rel="noreferrer"
                  >
                    opentelemetry-helm/opentelemetry-collector
                  </a>
                  <br />
                  <a
                    className="underline"
                    href="https://artifacthub.io/packages/helm/grafana/tempo-distributed"
                    target="_blank"
                    rel="noreferrer"
                  >
                    grafana/tempo-distributed
                  </a>
                  <br />
                  <a
                    className="underline"
                    href="https://artifacthub.io/packages/helm/grafana/grafana"
                    target="_blank"
                    rel="noreferrer"
                  >
                    grafana/grafana
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Grafana Tempo can be either deployed in monolithic mode or in
                  microservices mode. Grafana Tempo distributed uses the
                  microservices deployment mode, and consists of different
                  components: ingestor, distributor, compactor, querier, query
                  frontend, and memcache.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  As described in the Grafana Tempo documentation [5], you can
                  use different approaches for granting Grafana Tempo access to
                  an Azure Storage Account: the Storage Account access key, an
                  Azure Managed Identity, or via Azure Workload Identity.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The project makes use of the Azure Workload Identity, allowing
                  the Grafana Tempo pods to connect to the Azure Storage
                  Account. Using the Workload Identity federation [6], the
                  application makes use of trusted tokens to access the
                  Microsoft Entra protected resources. The design of the
                  solution is following.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The Azure Kubernetes Service resource is configured with Azure
                  Workload Identity and OIDC issuer:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-javascript">
                    resource "azurerm_kubernetes_cluster" "aks" &#123;
                    <br />
                    &nbsp;&nbsp;&#91;...&#93;
                    <br />
                    &nbsp;&nbsp;oidc_issuer_enabled = true
                    <br />
                    &nbsp;&nbsp;workload_identity_enabled = true
                    <br />
                    &#125;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The RBAC role "Storage Blob Data Contributor" is granted to
                  the User Managed Identity on the Azure Storage Account.
                  <br />
                  The Grafana Tempo Helm Chart is configured with a service
                  account connected to the User Managed Identity:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-yaml">
                    serviceAccount:
                    <br />
                    &nbsp;&nbsp;create: true
                    <br />
                    &nbsp;&nbsp;name: "tempo-service-account"
                    <br />
                    &nbsp;&nbsp;annotations:
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;azure.workload.identity/client-id:
                    "value"
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;azure.workload.identity/tenant-id:
                    "value"
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following label is applied to the Tempo pods in the
                  Grafana Tempo Helm Chart:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-yaml">
                    tempo:
                    <br />
                    &nbsp;&nbsp;podLabels:
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;azure.workload.identity/use: "true"
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The storage backend in the Grafana Tempo Helm Chart is
                  configured with the Azure Storage Account name and the
                  container name where the traces are going to be stored:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-yaml">
                    storage:
                    <br />
                    &nbsp;&nbsp;trace:
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;backend: azure
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;azure:
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;container_name: "traces"
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;storage_account_name:
                    "value"
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;use_federated_token:
                    true
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  A federated identity credentials resource in Azure establishes
                  a trust relationship between the Azure Kubernetes Service, the
                  User Managed Identity, and the service account deployed in
                  Kubernetes.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The Helm Charts in the project are configured for a simple
                  test scenario. Specifically, the Grafana Tempo components are
                  configured with one single replica. It's interesting to notice
                  that with the default configuration in place, Grafana Tempo
                  distributor throws the following error:
                </p>
                <p className="bg-blue-100 dark:bg-blue-900 border-blue-500 text-blue-700 dark:text-blue-100 leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert px-4 py-2">
                  msg="pusher failed to consume trace data" err="at least 2 live
                  replicas required, could only find 1"
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following configuration must then be applied in the
                  Grafana Tempo Helm Chart:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-yaml">
                    ingester:
                    <br />
                    &nbsp;&nbsp;config:
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;replication_factor: 1
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://opentelemetry.io/docs/what-is-opentelemetry/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    opentelemetry
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://opentelemetry.io/docs/collector/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    opentelemetry-collector
                  </a>
                  <br />
                  [3]{" "}
                  <a
                    className="underline"
                    href="https://grafana.com/oss/tempo/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    grafana-tempo
                  </a>
                  <br />
                  [4]{" "}
                  <a
                    className="underline"
                    href="https://grafana.com/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    grafana
                  </a>
                  <br />
                  [5]{" "}
                  <a
                    className="underline"
                    href="https://grafana.com/docs/tempo/latest/configuration/hosted-storage/azure/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    grafana-tempo-storage
                  </a>
                  <br />
                  [6]{" "}
                  <a
                    className="underline"
                    href="https://learn.microsoft.com/en-us/entra/workload-id/workload-identity-federation"
                    target="_blank"
                    rel="noreferrer"
                  >
                    workload-identity-federation
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post21;
