import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import active_recall_image from "./images/active_recall.jpeg";
import learn_process_image from "./images/learn_process.jpeg";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post14() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["active-recall"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Active recall learning web application</title>
        <meta
          name="description"
          content="Active recall learning web application"
        />
        <meta name="keywords" content="active-recall" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta
          property="og:title"
          content="Active recall learning web application"
        />
        <meta
          property="og:description"
          content="Active recall learning web application"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Active recall learning web application
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Sep 19, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I developed a web application for improving the learning
                  process, based on the testing effect method (also known as
                  active recall). This method promotes efficient learning and
                  focuses on increasing the long-term memory [1].
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Flashcards [2] are an application of the active recall method,
                  and typically consist of a question (or definition) on one
                  side of the card and an answer on the other side of the card.
                  The web application allows you to collect and study your own
                  cards.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The user of the application must login in order to access to
                  the private profile. The application makes use of Oauth 2.0
                  and JWT authentication, and supports the local authentication
                  and Google authentication.
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img src={active_recall_image} alt="Active Recall"></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The cards are collected in Workspaces, that provide a
                  categorisation for the cards. The user could for example
                  create a Workspace for learning a new language and another
                  Workspace for learning history. The user can create, update,
                  and delete cards in one Workspace, and search for cards based
                  on the content. The current version of the application does
                  not support media content or mathematical expressions in the
                  cards.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Workspaces are private to one single user, and is recommended
                  to use the Export Workspace and the Import Workspace features
                  to backup the cards or share a workspace with another user.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The web application provides a learn feature for studying the
                  cards grouped in one Workspace. The application uses the
                  SuperMemo SM-2 spaced repetition algorithm [3], that
                  implements the spaced repetition learning method. The
                  application stores the review date information for each
                  learned card, in order to exhibit the cards to the user during
                  the learn process, based on a time interval factor.
                </p>
                <div className="leading-7 mt-4 text-center w-full image-container">
                  <img src={learn_process_image} alt="Learn Process"></img>
                </div>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The user is asked to provide a self evaluation of the
                  knowledge for each card during the learn process, using a
                  rating mechanism (from 1 to 5). The last rating and the past
                  successful evaluations of the card are used to calculate the
                  new time interval associated with the card. If the user
                  provides the correct answer more often, the time interval for
                  the card will increment. On the other side, if the user does
                  not answer correctly, the application will exhibit the card
                  more frequently during the study process.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I created the web application using React on the fronted, and
                  Spring Boot with Kotlin language on the backend. The GitHub
                  repository with the web application is the following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/learn-tool"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/learn-tool
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The application is bundled with Docker images and is executed
                  using Docker Compose. In addition, on a production deployment,
                  NGINX should be used as a load balancer for the web
                  application.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  A running version of the web application is the following:
                  <br />
                  <a
                    className="underline"
                    href="http://138.2.153.33/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    http://learn-tool
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  This specific instance of the web application supports just
                  the local authentication.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://en.wikipedia.org/wiki/Testing_effect"
                    target="_blank"
                    rel="noreferrer"
                  >
                    wiki/Testing_effect
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://en.wikipedia.org/wiki/Flashcard"
                    target="_blank"
                    rel="noreferrer"
                  >
                    wiki/Flashcard
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [3]{" "}
                  <a
                    className="underline"
                    href="https://en.wikipedia.org/wiki/SuperMemo"
                    target="_blank"
                    rel="noreferrer"
                  >
                    wiki/SuperMemo
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post14;
