import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post20() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["jackrabbit-oak"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>JackRabbit Oak CND Update</title>
        <meta name="description" content="JackRabbit Oak CND Update" />
        <meta name="keywords" content="jackrabbit-oak" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="JackRabbit Oak CND Update" />
        <meta property="og:description" content="JackRabbit Oak CND Update" />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                JackRabbit Oak CND Update
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Feb 28, 2025
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Apache Jackrabbit Oak is a scalable content repository that
                  implements the JCR 2.0 (Java Content Repository)
                  specification. It features a modular architecture for storing
                  and accessing data, supports large-scale repositories, and is
                  optimized for horizontal scaling [1].
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The JCR 2.0 specification [2] defines an abstract model for
                  data storage, commonly used by content-oriented applications.
                  The specification addresses applications that handle
                  structured, unstructured, and semi-structured data. The
                  repository model for JCR defines an effective approach for
                  accessing both large binary data and structured hierarchical
                  data. JCR incorporates many features commonly found in RDBMS
                  and file systems, particularly support for querying, access
                  control, versioning, and locking.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Each node in a Jackrabbit Oak workspace has a primary node
                  type assigned upon creation that defines its structure,
                  including child node types and properties. Mixin node types
                  typically specify additional properties related to a
                  capability added to the node.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Jackrabbit Oak provides predefined node types, such as
                  nt:base, nt:file, and nt:unstructured. In addition, the
                  Compact Namespace and Node Type Definition (CND) notation
                  provides a syntax for defining custom node types, mixins, and
                  properties. These node type definitions can be registered
                  using a cnd.config file via the CndImporter class. The schema
                  in the cnd.config file allows the declaration of custom
                  namespaces, ensuring uniqueness in node type definitions.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  A common use case when working with custom node types is the
                  following: you create a repository based on an initial
                  definition for the custom node types and develop an
                  application that creates nodes inside the repository using
                  these node types; at a certain point in time, you need to
                  adjust the custom node type definitions to support new
                  business requirements and features by adding, modifying, or
                  removing node types or properties.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The main challenge in this scenario is that Jackrabbit Oak
                  does not allow direct updates to registered node types because
                  it enforces schema validation, and changes to node definitions
                  can break existing nodes.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I developed a simple tool that provides a solution for
                  updating custom node types and migrating data using a new
                  custom node type definition. The migration tool guarantees
                  that the names of the target node types will be the same as
                  those of the initial node types.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The GitHub repository of the project is the following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/Jackrabbit-Oak-repository-upgrade"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/Jackrabbit-Oak-repository-upgrade
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  One assumption of the migration tool is the use of a
                  repository with type Oak Segment Tar (Tar file based Segment
                  NodeStore).
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The tool requires a proper definition of the migration steps,
                  as updates in the Node Type Definition file must be applied
                  one by one, and each step needs to be adapted based on the
                  operation to perform. For example, the first step could be
                  removing a property from a mixin, and the second step could be
                  creating a new mixin with specific properties.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Each step of the migration is bundled as follows:
                  <ul>
                    <li>Register the new namespace in the repository</li>
                    <li>Register the node types from the new namespace</li>
                    <li>Migrate the node types to the new namespace</li>
                    <li>Migrate the nodes to the new namespace</li>
                    <li>Unregister the node types from the old namespace</li>
                  </ul>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The application provides a complete example of repository
                  migration, demonstrating the transition from an initial CND
                  file to a final CND file. The example is useful in order to
                  understand how to configure the migration tool, and use the
                  available runners. The tool offers two runners:
                  OakSimpleMigratorRunner designed for simple node migrations
                  that do not require to use a new namespace, and
                  OakStepByStepMigratorRunner intended for more complex,
                  non-trivial repository changes.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The application also demonstrates the migration of nodes
                  versions for the custom type app:file. Since the frozenNode of
                  each node version is immutable, every version for each node
                  must be recreated. This process involves applying the version
                  properties to the node, creating a new node version, and
                  assigning the labels to the new version.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://jackrabbit.apache.org/oak/docs/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    jackrabbit-oak
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://developer.adobe.com/experience-manager/reference-materials/spec/jcr/2.0/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    jcr-2.0
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post20;
