import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-java";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post9() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["cad", "javafx"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>CAD Desktop Application in JavaFX</title>
        <meta name="description" content="CAD Desktop Application in JavaFX" />
        <meta name="keywords" content="cad, javafx" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta property="og:title" content="CAD Desktop Application in JavaFX" />
        <meta
          property="og:description"
          content="CAD Desktop Application in JavaFX"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                CAD Desktop Application in JavaFX
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Sep 4, 2024
                </time>
              </span>
              <div className="line-break"></div>
              <div className="articleBody">
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  In the past I worked with AWT and Swing in Java, and more
                  recently I wanted to experiment with JavaFX creating a simple
                  CAD desktop application.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  JavaFX [1] is a toolkit for developing rich client
                  applications, for desktop, mobile, and embedded systems built
                  on Java. Starting from Java JDK 11, JavaFX became part of the
                  OpenJDK project.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  For my CAD application I decided to use Java 21 LTS, and Maven
                  for managing the required JavaFX libraries.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  JavaFX is a set of graphics and media packages and the main
                  architecture components are the JavaFX API and the scene
                  graph. The scene graph consists of a hierarchical tree of
                  nodes that represents all of the visual elements on the
                  application's user interface. For each element you can apply a
                  state (for example the position on the screen), and effects
                  that change the appearance of the element.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  JavaFX provides UI controls, cascading style sheets (CSS),
                  layout containers, transformations, and visual effects for the
                  graphical elements.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Many different libraries are available in order to customise
                  the look-and-feel of your application. I used the Ikonli
                  Material Design 2 [2] for the icons in the application, and
                  atlantafx [3] as the CSS theme for the visual components.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The layout of the application can be composed either using the
                  JavaFX APIs or JavaFX FXML. The second option is useful to
                  separate the UI layout code from the application logic.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I found the JavaFX APIs extremely easy and intuitive to use,
                  and the JavaFX framework provides out of the box support for
                  2D and 3D graphics, in my case I was interested on the shapes
                  for 2D modelling.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  On high level, the JavaFX application is composed by a Stage
                  and a Scene. The JavaFX Stage class is the top level JavaFX
                  container, and represents a window in a JavaFX desktop
                  application. Inside a JavaFX Stage you can add a JavaFX Scene,
                  which represents the content of the window. Apart the root
                  Stage object, you can create additional Stage objects when the
                  application needs to open new windows.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following is a simple example of a JavaFX application:
                </p>
                <pre className="mt-4 text-left h-96 w-full">
                  <code className="language-java">
                    import javafx.scene.control.Label;
                    <br />
                    import javafx.scene.layout.Pane;
                    <br />
                    import javafx.scene.Group;
                    <br />
                    import javafx.scene.Scene;
                    <br />
                    import javafx.scene.shape.Line
                    <br />
                    import javafx.scene.shape.Shape
                    <br />
                    import javafx.stage.Stage;
                    <br />
                    <br />
                    private Pane pane;
                    <br />
                    <br />
                    public class App extends Application &#123;
                    <br />
                    &nbsp;&nbsp;&#64;Override
                    <br />
                    &nbsp;&nbsp;public void start(Stage stage) &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;stage.setTitle("Simple CAD");
                    <br />
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;Label label = new Label("Label");
                    <br />
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.pane = new Pane();
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;pane.setMinSize(500, 400);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;pane.setOnMouseClicked(this::handleMouseClick);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;pane.getChildren().add(label);
                    <br />
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;Group root = new Group(pane);
                    <br />
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;var scene = new Scene(root, 600,
                    500);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;stage.setScene(scene);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;stage.show();
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    <br />
                    &nbsp;&nbsp;private void handleMouseClick(MouseEvent event)
                    &#123;
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;var line = new Line();
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;line.setStartX(event.getX());
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;line.setStartY(event.getY());
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;line.setEndX(event.getX()+10);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;line.setEndY(event.getY()+5);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;line.setStrokeWidth(1.0);
                    <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;this.pane.getChildren().add(line);
                    <br />
                    &nbsp;&nbsp;&#125;
                    <br />
                    &#125;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The Scene contains one Group object with one Pane. In the
                  previous example the shapes are added to the Pane on mouse
                  click event.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following is the GitHub repository of the CAD application:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/simple-cad"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/simple-cad
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I used an events-based architecture for the CAD application in
                  order to keep a separation of concern and decoupling the
                  different modules of the system.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://openjfx.io/openjfx-docs/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    openjfx-docs
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://kordamp.org/ikonli/cheat-sheet-materialdesign2.html"
                    target="_blank"
                    rel="noreferrer"
                  >
                    ikonli/materialdesign2
                  </a>
                  <br />
                  [3]{" "}
                  <a
                    className="underline"
                    href="https://github.com/mkpaz/atlantafx"
                    target="_blank"
                    rel="noreferrer"
                  >
                    mkpaz/atlantafx
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post9;
